export const urlValidation = {
  required: "  URL",
  validate: (value) => {
    if (value.match(/^(ftp|http|https):\/\/[^ "]+$/)) {
      return true;
    }
    return "  URL";
  },
};

export const commenTextValidation = {
  required: "   6 ",
  validate: (value) => {
    if (value.match(/[0-9a-zA-Z--\s]{6,}/)) {
      return true;
    }
    return "   6 ";
  },
};

export const emailValidation = {
  required: "  e-mail ",
  validate: (value) => {
    if (
      value.match(
        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/iu
      )
    ) {
      return true;
    }
    return "  e-mail ";
  },
};

export const passwordValidation = {
  required:
    "     ,       ",
  validate: (value) => {
    if (value.match(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/)) {
      return true;
    }
    return "     ,       ";
  },
};
