import React, { useState } from "react";
import { useForm, Controller } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import api from "../API";
import { emailValidation, passwordValidation } from "../validation";
import {
  Box,
  Button,
  DialogContent,
  Dialog,
  TextField,
  DialogTitle,
  DialogContentText,
  DialogActions,
} from "@mui/material";

function SignUpForm({ close }) {
  const [openSuccess, setOpenSuccess] = useState(false);
  const [openError, setOpenError] = useState(false);

  const handleClickOpenError = () => {
    setOpenError(true);
  };

  const handleClickCloseError = () => {
    setOpenError(false);
  };

  const handleClickOpenSuccess = () => {
    setOpenSuccess(true);
  };

  const handleClickCloseSuccess = () => {
    close();
    navigate("/");
  };

  const navigate = useNavigate();

  const redirect = () => {
    close();
    navigate("/login");
  };

  const {
    handleSubmit,
    formState: { errors },
    control,
  } = useForm({
    mode: "onChange",
    defaultValues: {
      email: "",
      group: "group-10",
      password: "",
    },
  });

  const signUp = (data) => {
    const { email, group, password } = data;
    api
      .signUp(email, group, password)
      .then(() => {
        handleClickOpenSuccess();
      })
      .catch((obj) => {
        if (obj.status === 409) {
          handleClickOpenError();
        } else {
          console.log(obj);
        }
      });
  };

  return (
    <>
      <DialogTitle>  </DialogTitle>
      <DialogContent>
        <DialogContentText>
             ,  
        </DialogContentText>
        <Box component={"form"} onSubmit={handleSubmit(signUp)}>
          <Controller
            name="email"
            control={control}
            rules={emailValidation}
            render={({ field: { onChange, value } }) => (
              <TextField
                label="E-mail "
                size="medium"
                margin="normal"
                fullWidth
                value={value}
                onChange={(e) => onChange(e)}
                error={!!errors.email?.message}
                helperText={errors.email?.message}
              />
            )}
          />
          <Controller
            name="group"
            control={control}
            render={({ field: { onChange, value } }) => (
              <TextField
                label=" "
                size="medium"
                margin="normal"
                fullWidth
                disabled
                value={value}
                onChange={(e) => onChange(e)}
                error={!!errors.group?.message}
                helperText={errors.group?.message}
              />
            )}
          />
          <Controller
            name="password"
            control={control}
            rules={passwordValidation}
            render={({ field: { onChange, value } }) => (
              <TextField
                label=""
                type="password"
                size="medium"
                margin="normal"
                fullWidth
                value={value}
                onChange={(e) => onChange(e)}
                error={!!errors.password?.message}
                helperText={errors.password?.message}
              />
            )}
          />
          <DialogActions>
            <Button variant="contained" type="submit">
               
            </Button>
            <Button onClick={redirect}>  </Button>
            <Button onClick={close}></Button>
          </DialogActions>
        </Box>
      </DialogContent>

      <Dialog
        open={openError}
        onClose={handleClickCloseError}
        fullWidth={true}
        maxWidth="sm"
      >
        <DialogTitle></DialogTitle>
        <DialogContent>   email  </DialogContent>
        <DialogActions>
          <Button onClick={handleClickCloseError} variant="contained">
            Ok
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={openSuccess}
        onClose={handleClickCloseSuccess}
        fullWidth={true}
        maxWidth="sm"
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
            .      
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClickCloseSuccess} variant="contained">
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}

export default SignUpForm;
