import { Discuss } from "react-loader-spinner";
import s from "./style.module.css";

function Spinner() {
  // - type -   (Puff, Audio, BallTriangle, Bars, Circles, Grid, Hearts, Oval, Rings, TailSpin, ThreeDots)
  // - color -  
  // - height -    
  // - width -    
  // - timeout -  ( ),      

  return (
    <div className={s.spinner}>
      <Discuss
        type="Puff"
        color="#00BFFF"
        height={200}
        width={200}
        timeout={30000} //3 secs
      />
    </div>
  );
}

export default Spinner;
